/* Utilities */
body {
  font-family: "Sansita Swashed", cursive;
  color: white;
  background-color: black;
}

a {
  cursor: pointer;
}

i {
  color: rgb(212, 105, 159);
  cursor: pointer;
}

p {
  color: white;
}

span {
  color: whitesmoke;
}
h2 {
  color: pink;
}

h4 {
  color: white;
}

h6 {
  color: black;
  font-size: 19px;
}

.hidden {
  display: none;
}

.highlight {
  color: black;
}
/* Navbar */
#clear-cart {
  border-color: rgb(212, 105, 159);
  color: white;
}

#checkout {
  border-color: rgb(212, 105, 159);
  color: white;
}

.fa-search {
  padding: 11px;
  margin-left: -7px;
  background-color: white;
}

.rules-btn {
  margin-left: 11rem;
  width: 100px;
  border-radius: 10px;
  border-color: hotpink;
  background-color: transparent;
  color: white;
  padding: 3px;
}

.rules {
  position: absolute;
  top: 73%;
  left: 61%;
  width: 300px;
  height: 262px;
  background-color: #292b2c;
  border-radius: 10px;
  clip-path: polygon(
    0 5%,
    45% 5%,
    50% 0,
    50% 0,
    55% 5%,
    100% 5%,
    100% 85%,
    100% 100%,
    85% 100%,
    15% 100%,
    0 100%,
    0% 85%
  );
}
.rules li:first-child {
  margin-top: 1rem;
}
.rules li {
  background-color: #292b2c;
  border-top: solid rgb(212, 105, 159) 1px;
  border-bottom: solid rgb(212, 105, 159) 1px;
  color: rgb(212, 105, 159);
}
/* Images */
.small {
  height: 50px;
  width: 50px;
}

.medium {
  width: 100%;
  height: 230px;
}

/* Checkout */
.checkout {
  margin-left: 5rem !important;
}

@media only screen and (max-width: 1000px) {
  /* Uitilities */
  i {
    font-size: 15px;
  }

  a {
    font-size: 15px !important;
  }

  h2 {
    font-size: 20px;
    margin-left: 20px;
    padding-right: 3px;
  }
  h4 {
    font-size: 16px;
  }
  h6 {
    font-size: 11px;
    margin-top: 8px;
  }
  /* Images */
  .small {
    height: 50px;
    width: 50px;
  }

  .medium {
    margin-left: 20px;
    width: 100px;
    height: 100px;
  }

  /* Navbar */
  .navbar {
    padding: 0;
  }
  .navbar-brand {
    width: 50px;
    height: 50px;
    padding-right: 6px;
  }

  .search {
    display: none;
  }

  .rules-btn {
    margin-left: 20px;
    width: 60px;
    height: 30px;
    font-size: 11px;
  }

  .rules {
    position: absolute;
    top: 45%;
    left: 53%;
    width: 150px;
    height: 200px;
    font-size: 15px;
    background-color: #292b2c;
    border-radius: 10px;
    clip-path: polygon(
      0 5%,
      45% 5%,
      50% 0,
      50% 0,
      55% 5%,
      100% 5%,
      100% 85%,
      100% 100%,
      85% 100%,
      15% 100%,
      0 100%,
      0% 85%
    );
  }
  .rules li:first-child {
    margin-top: 0.8rem;
  }
  .rules li {
    margin: 0;
    padding: 0;
  }

  /* Store */

  #store {
    margin-top: 50px !important;
  }
  .store-items {
    width: 100vw !important;
    display: grid !important;
    grid-template-columns: repeat(2, 1fr) !important;
    padding-bottom: 0 !important;
  }

  .col-md-3 {
    width: 75%;
    margin-left: 10px;
    padding: 0;
  }

  .card-body {
    padding: 5px;
  }

  .btn-danger {
    padding: 0 10px;
  }

  #cart-info {
    margin: 0;
    display: inline;
    width: 50%;
  }

  /* Cart */
  .dropdown-menu {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
  .cart-item-text {
    font-size: 15px !important;
  }

  .cart-item {
    padding: 10px !important;
    margin: 10px !important;
  }

  .cart-total-container {
    margin-top: 5px !important;
    padding: 5px !important;
  }

  .checkout {
    width: 75% !important;
  }

  .margin {
    margin-left: -70px !important;
  }

  .download {
    margin-left: 25% !important;
  }
}
